import rate from './rate.vue';
export default {
	components: {
		Rate: rate
	},
	props: {
		typeCheck: {
			type: Boolean,
			default: false,
		},
		isChecked: {
			type: Boolean,
			default: false,
		},
		dragonImage: {
			default: require('@/assets/myAssets/dragon.png')
		},
		dragonId: {
			type: String,
			default: ''
		},
		dragonName: {
			type: String,
			default: ''
		},
		cardType: {
			type: [Number, String]
		},
		skills: {
			type: Array,
			default: () => [],
			validator(skills) {
				let lengthNum = skills.length;
				return lengthNum === 4 || lengthNum === 0;
			}
		},
		stars: {
			type: Number,
			default: 0
		},
		dragonHatch: {
			type: [String, Number],
			default: '0'
		},
		nft: {
			type: Number,
		},
		doller: {
			type: Number,
		}
	},
	computed: {
		cardTypeClass() {
			const types = {
				1: 'cli-1',
				2: 'cli-2',
				3: 'cli-3',
				4: 'cli-4'
			}
			return types[this.cardType];
		},
		showPayModule() {
			return this.nft && this.doller;
		},
	},
	methods: {
		choice() {
			this.$emit('choice')
		},
		toDecimal2(x) {
			var f = parseFloat(x);
			if (isNaN(f)) {
				return false;
			}
			var f = Math.round(x * 100) / 100;
			var s = f.toString();
			var rs = s.indexOf('.');
			if (rs < 0) {
				rs = s.length;
				s += '.';
			}
			while (s.length <= rs + 2) {
				s += '0';
			}
			return s;
		}
	}
};
